.alert-dialog .MuiDialog-paper {
  padding: 25px 0px !important;
  min-height: 300px !important;
  width: 600px; }
  .alert-dialog .MuiDialog-paper .alert--wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 600px;
    padding: 8px 24px !important;
    font-size: 18px; }
    .alert-dialog .MuiDialog-paper .alert--wrapper .alert--icon {
      height: 120px;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 24px;
      padding-bottom: 16px;
      animation: appear 0.45s ease-in-out; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--icon img {
        height: 100%; }
    .alert-dialog .MuiDialog-paper .alert--wrapper .alert--title {
      text-align: center;
      color: #616161;
      font-weight: bold;
      margin: 20px 8px; }
    .alert-dialog .MuiDialog-paper .alert--wrapper .alert--subtitle {
      text-align: center;
      color: #757575;
      font-weight: 300;
      margin: 16px 0px 40px;
      word-wrap: break-word; }
    .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group {
      padding: 24px 12px 4px;
      border-top: 1px solid #eeeeee;
      display: flex;
      justify-content: center;
      align-items: center; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group button {
        color: white;
        border: none;
        padding: 12px 24px;
        min-width: 180px;
        border-radius: 50px;
        font-weight: 500;
        font-size: 16px;
        transition: background 0.2s ease-in-out;
        cursor: pointer; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group button:focus {
          outline: none; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.success {
        background: #81c784; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.success:hover {
          background: #66bb6a; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.error {
        background: #e57373; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.error:hover {
          background: #ef5350; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.info {
        background: #4dd0e1; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--ok.info:hover {
          background: #26c6da; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--confirm {
        margin-right: 8px; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--confirm.warning {
          background: #ffca28; }
          .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--confirm.warning:hover {
            background: #ffc107; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--confirm.question {
          background: #b39ddb; }
          .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--confirm.question:hover {
            background: #9575cd; }
      .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--cancel {
        margin-left: 8px;
        background: #bdbdbd; }
        .alert-dialog .MuiDialog-paper .alert--wrapper .alert--button-group .btn--cancel:hover {
          background: #9e9e9e; }

@media not all and (min-width: 767px) {
  .alert--title {
    margin: 8px 0;
    font-size: 24px; }
  .alert--subtitle {
    margin: 8px 0 28px;
    font-size: 14px; }
  .alert--icon {
    height: 80px; }
  .alert--button-group {
    flex-direction: column; }
    .alert--button-group button {
      padding: 10px 20px;
      min-width: 100px;
      font-size: 14px; }
    .alert--button-group .btn--confirm {
      margin: 0 0 8px; }
    .alert--button-group .btn--cancel {
      margin: 0; } }

@keyframes appear {
  from {
    opacity: 0.3;
    transform: translateY(16px); }
  to {
    opacity: 1;
    transform: translateY(0); } }
